export default function Login() {

  return (
    <div className="flex flex-col text-black bg-black text-2xl items-center justify-center h-screen">
      <div className="flex flex-col gap-4 p-4 border-2 border-white rounded-lg"> 
        <h2 className="items-center justify-center text-center text-white">Login</h2>
        <form action="" method="post" className="flex flex-col bg-gray-300 w-full border-black rounded-lg p-4 gap-4">
          <input
            className="p-2 pb-0"
            type="text"
            name="username"
            placeholder="Username"
            required
            minLength={3}
            maxLength={30}
          />
          <hr />
          <input
            className="p-2  pb-0"
            type="password"
            name="password"
            placeholder="Password"
            required
            minLength={5}
            maxLength={15}
          />
          <hr />
          <input className="p-2  pb-0"  type="text" name="email" placeholder="Email" required />
          <hr />
          <button className="p-2  pb-0" type="submit" className="border-black rounded-lg bg-white p-3 hover:bg-gray-200">Login</button>
        </form>
      </div>
    </div>
  );
}
